.NavBar{
  height:100vh
}

.NavBar .nav{
  height:100%;
  display:-webkit-flex;
  display:flex
}

.NavBar .nav .profile{
  width:100%;
  height:15%;
  margin-top:.5rem;
  display:grid;
  grid-template-columns:50% 25% 25%
}

.NavBar .nav .profile .avatar{
  width:90%;
  height:100%;
  margin-left:.5rem;
  grid-column-start:1;
  grid-column-end:2
}

.NavBar .nav .profile .light-dark-toggle{
  width:90%;
  height:100%;
  grid-column-start:2;
  grid-column-end:3;
  margin-top:1.1rem;
  margin-left:1rem
}

.NavBar .nav .profile .light-dark-toggle .checkbox{
  opacity:0;
  position:absolute
}

.NavBar .nav .profile .light-dark-toggle .checkbox:checked+.label .ball{
  -webkit-transform:translateX(24px);
  transform:translateX(24px)
}

.NavBar .nav .profile .light-dark-toggle .label{
  background-color:#111;
  border-radius:50px;
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:space-between;
  justify-content:space-between;
  position:relative;
  padding:5px;
  height:26px;
  width:50px;
  -webkit-transform:scale(1);
  transform:scale(1)
}

.NavBar .nav .profile .light-dark-toggle .ball{
  height:22px;
  width:22px;
  top:2px;
  left:2px;
  border-radius:50px;
  background-color:#fff;
  position:absolute;
  transition:-webkit-transform .2s linear;
  transition:transform .2s linear;
  transition:transform .2s linear, -webkit-transform .2s linear
}

.NavBar .nav .profile .light-dark-toggle .fa-moon{
  color:#f1c40f
}

.NavBar .nav .profile .light-dark-toggle .fa-sun{
  color:#f39c12
}

.NavBar .nav .profile .lang-list{
  grid-column-start:3;
  grid-column-end:4;
  margin-top:.01rem;
  margin-left:1.1rem
}

.NavBar .nav .profile .lang-list .dropdown-toggle::after{
  display:none;
  border-top:none;
  border-right:none;
  border-left:none;
  border-bottom:none
}

.NavBar .nav .profile .lang-list .btn{
  background-color:#111;
  color:#fff
}

.NavBar .nav .nav-items{
  width:100%;
  height:70%
}

.NavBar .nav .nav-items .nav-container{
  margin-top:10rem;
  margin-bottom:10rem
}

.NavBar .nav .nav-items .nav-item{
  list-style:none;
  text-align:center
}

.NavBar .nav .nav-items .nav-item a{
  text-decoration:none;
  font-size:inherit;
  color:#000;
  display:block;
  padding:.5rem 0;
  position:relative;
  margin:0 0 15px 0
}

.NavBar .nav .nav-items .nav-item a::before{
  content:"";
  position:absolute;
  top:0;
  left:0;
  width:.4rem;
  height:100%;
  background-color:#ffd00033;
  -webkit-transform-origin:bottom;
  transform-origin:bottom;
  -webkit-transform:scale(0);
  transform:scale(0);
  overflow-x:hidden;
  transition:.2s width .5s cubic-bezier(1, -0.16, 0.6, 1.35),-webkit-transform .2s;
  transition:transform .2s,.2s width .5s cubic-bezier(1, -0.16, 0.6, 1.35);
  transition:transform .2s,.2s width .5s cubic-bezier(1, -0.16, 0.6, 1.35),-webkit-transform .2s
}

.NavBar .nav .nav-items .nav-item a:hover::before{
  width:100%;
  -webkit-transform:scale(1);
  transform:scale(1)
}

.NavBar .nav .footer-div{
  width:100%;
